//itemId id      ItemPage         ""
import * as actions from '../actions/actions'


const initialState = {
    itemId: null,
    qty: 0,
}

const reducer = (state = initialState, action) => {
    
    switch (action.type) {
        case actions.ADD_ITEM:
            return {
                ...state,
                itemId: action.payload
            };
         case actions.QTY_SHOPPINGBAG:
            return {
                ...state,
                qty: action.payload
            };
        default:
            return state;
    }
}

export default reducer;